import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Empty, Input, Collapse, Select, Tooltip } from "antd";

import Popconfirms from "../../../../components/feedback/popconfirm";
// import Box from '../../components/utility/box';
// import HelperText from "../../../../components/utility/helper-text";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/asset/assetType/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  customCollapseStyle
} from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const redirectTo = "/dashboard/setup/menus";

class AssetType extends Component {
  state = {
    filter: {
      id_asset_fiscal: [],
      id_asset_category: [],
      transaction_type: [],
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getAssetTypes } = this.props;
    getAssetTypes();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.assetType.error) {
      notification("error", this.props.assetType.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.assetType.success) {
      notification("success", this.props.assetType.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    console.log(value);
    const { getAssetTypes } = this.props;
    this.setState({
      filter: {
        id_asset_fiscal: [],
        id_asset_category: [],
        transaction_type: [],
        pagination: null
      }
    });
    getAssetTypes(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getAssetTypes } = this.props;
    this.setState({
      filter: {
        id_asset_fiscal: [],
        id_asset_category: [],
        transaction_type: [],
        pagination: null
      }
    });
    this.setState({ search: null });
    getAssetTypes();
  };

  // Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getAssetTypes } = this.props;
    //const { current_page } = this.props.assetType;
    const { filter } = this.state;
    let string = "";

    if (filter.id_asset_fiscal) {
      string += "id_asset_fiscal=" + filter.id_asset_fiscal;
    }
    if (filter.id_asset_category) {
      string
        ? (string += "&id_asset_category=" + filter.id_asset_category)
        : (string += "id_asset_category=" + filter.id_asset_category);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    this.setState({ search: null });
    getAssetTypes(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getAssetTypes } = this.props;
    this.setState({
      filter: {
        id_asset_fiscal: [],
        transaction_type: [],
        id_asset_category: [],
        pagination: null
      }
    });
    this.setState({ search: null });
    getAssetTypes();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateAssetType(id);
    } else {
      this.props.deactivateAssetType(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getAssetTypes } = this.props;
    const { search } = this.props.assetType;
    const { filter } = this.props.assetType;
    getAssetTypes(page, search, filter);
  };

  listAction = {
    title: "",
    dataIndex: "id_asset_type",
    rowKey: "id_asset_type",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {row.is_active ? (
          <Popconfirms
            title={<IntlMessages id="assetTypes.popconfirm.deactivate" />}
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
          <Popconfirms
            title={<IntlMessages id="assetTypes.popconfirm.activate" />}
            onConfirm={this.handleRecord.bind(this, "activate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.activate" />}
            >
              <Button size="small" className="invoiceRstBtn">
                <i className="ion-refresh" />
              </Button>
            </Tooltip>
          </Popconfirms>
        )}
      </div>
    )
  };

  render() {
    // console.log(this.state.filter);
    const columns = [
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "asset_type_code",
        rowKey: "asset_type_code",
        width: "10%",
        //fixed: "left",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "name" }),
        dataIndex: "asset_type_name",
        rowKey: "asset_type_name",
        width: "20%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "sidebar.assetCategory" }),
        dataIndex: "asset_category.asset_category_name",
        rowKey: "asset_category.asset_category_name",
        width: "20%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "sidebar.assetFiscal" }),
        dataIndex: "asset_fiscal.asset_fiscal_name",
        rowKey: "asset_fiscal.asset_fiscal_name",
        width: "20%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "assetTypes.add.depreciationMethod" }),
        dataIndex: "depreciation_method",
        rowKey: "depreciation_method",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "warning";
            display = getLanguage({ id: "Non Depreciable" });
          } else if (text === 2) {
            className = "active";
            display = getLanguage({ id: "Straight Line" });
          } else {
            className = "primary";
            display = getLanguage({ id: "Double Declining Method" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "assetTypes.add.estimatedLifeYear" }),
        dataIndex: "estimated_life_year",
        rowKey: "estimated_life_year",
        width: "10%",
        align: "center",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "assetTypes.add.depreciationRate" }),
        dataIndex: "depreciation_rate",
        rowKey: "depreciation_rate",
        width: "10%",
        align: "center",
        render: text => <span>{parseFloat(text)}</span>
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "is_active",
        rowKey: "is_active",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "active";
            display = getLanguage({ id: "active" });
          } else {
            className = "nonactive";
            display = getLanguage({ id: "inactive" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    // Data setup
    const { asset_types } = this.props.assetType;
    //console.log(this.props.assetType); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (permissions.includes("Update Asset Type") && columns.length === 8) {
      columns.push(this.listAction);
    }

    Object.keys(asset_types).map((assetType, index) => {
      return dataSource.push({
        ...asset_types[assetType],
        key: assetType
      });
    });

    // Pagination Setup
    const { current_page, page_size, total, loading } = this.props.assetType;

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="assetTypes.pageHeader" />}
            marginBottom={true}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {permissions.includes("Create Asset Type") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button
                    // loading={loading}
                    // disabled={loading}
                    type="primary"
                    className="mateAddInvoiceBtn"
                  >
                    <IntlMessages id="assetTypes.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "code" }) + ", " + getLanguage({ id: "name" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <div className="isoFilterContent">
                  <div style={{ width: "50%" }}>
                    <label>
                      <IntlMessages id="assetFiscals.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_asset_fiscal"
                      )}
                      value={
                        this.state.filter.id_asset_fiscal
                          ? this.state.filter.id_asset_fiscal
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="assetTypes.filter.select.assetFiscal" />
                      }
                      loading={this.props.assetType.loading}
                      allowClear={true}
                    >
                      {this.props.assetType.asset_fiscals.map((data, key) => (
                        <Option key={key} value={data.id_asset_fiscal}>
                          {data.asset_fiscal_name}
                        </Option>
                      ))}
                    </Select>
                  </div>
                  <div style={{ width: "50%" }}>
                    <label>
                      <IntlMessages id="assetCategories.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_asset_category"
                      )}
                      value={
                        this.state.filter.id_asset_category
                          ? this.state.filter.id_asset_category
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={
                        <IntlMessages id="assetTypes.filter.select.assetCategory" />
                      }
                      loading={this.props.assetType.loading}
                      allowClear={true}
                    >
                      {this.props.assetType.asset_categories.map(
                        (data, key) => (
                          <Option key={key} value={data.id_asset_category}>
                            {data.asset_category_name}
                          </Option>
                        )
                      )}
                    </Select>
                  </div>
                </div>
                <div className="isoFilterContent" style={{ marginTop: "10px" }}>
                  <div style={{ width: "15%" }}>
                    <label>
                      <IntlMessages id="dataPerPage" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "pagination"
                      )}
                      value={
                        this.state.filter.pagination
                          ? this.state.filter.pagination
                          : page_size
                      }
                      style={{ width: "95%" }}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={this.props.assetType.loading}
                      // allowClear={true}
                      // showArrow={false}
                    >
                      <Option key={1} value={10}>
                        10
                      </Option>
                      <Option key={2} value={25}>
                        25
                      </Option>
                      <Option key={3} value={50}>
                        50
                      </Option>
                      <Option key={4} value={100}>
                        100
                      </Option>
                    </Select>
                  </div>

                  <div
                    style={{
                      width: "20%",
                      marginTop: "auto"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.submit" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </div>
                </div>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {asset_types.length === 0 && !this.props.assetType.loading ? (
              <Empty
                description={<IntlMessages id="assetTypes.table.noData" />}
              />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={this.props.assetType.loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    assetType: state.assetType,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(AssetType);
